@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
main {
  width: 90%;
  max-width: 360px;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  padding: 32px 24px 24px;
  border-radius: 16px;
  color: white;
}

.banner {
  width: 100%;
}

.banner > img {
  width: 100%;
  border-radius: 16px;
  border-bottom: 8px;
}

main > p {
  background-color: rgba(255, 255, 255, 0.3);
  width: 100%;
  padding: 8px;
  margin: 4px;
  border-radius: 8px;
  text-align: center;
}
html,
body {
  width: 100%;
  height: 100%;
  min-height: 600px;
  border: 0;
  margin: 0;
  padding: 0;
  outline: 0;
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(/SolSmith/static/media/background.ed5d3ae5.png);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow-y: auto;
}

* {
  outline: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

.flex {
  display: flex;
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}
